<template lang="html">
  <div class="System-role">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Username")}}</p>
        <Input  placeholder="" style="width: 150px"></Input>
      </Col>
      <Col :xs="12" :sm="12" :lg="6">
        <p>{{$t("common.Role_type")}} </p>
        <Select style="width:150px;text-align: center;">
            <Option v-for="item in category" :value="item.value" :label="item.label" :key="item.value"></Option>
        </Select>
      </Col>
      <Col :xs="12" :sm="12" :lg="12">
        <p>{{$t("common.login_time")}} </p>
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker> —
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t("common.Query")}}</Button>
      </Col>
    </Row>
    <Table highlight-row :columns="columns1" :data="data1" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  </div>
</template>

<script>
export default {
  name:'System_role',
  data (){
    return {
      category: [
          {
              value: 'All',
              label: this.$t("common.All")
          },
          {
              value: 'Yes',
              label: this.$t("common.Ordinary_role")
          },
          {
              value: 'No',
              label: this.$t("common.Management_role")
          }
      ],
      columns1 :[
        {
          title: this.$t("common.User_ID"),
          key: 'SID',
          align:'center',
        },
        {
            title: this.$t("common.Username"),
            key: 'user',
            align:'content',
        },
        {
            title: this.$t("common.Role_type"),
            key: 'category',
            align:'name',
        },
        {
            title: this.$t("common.Role_power"),
            key: 'power',
            align:'phone',
        },
        {
            title: this.$t("common.login_time"),
            key: 'date',
            align:'center'
        },
        {
            title: this.$t("common.Role_belong"),
            key: 'belong',
            align:'center',
            width:250
        },
        {
            title: this.$t('common.Operate'),
            key: 'operation',
            align:'center',
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                      props: {
                          type: 'text',
                          size: 'small'
                      },
                      style: {
                          color:'#00CCFF'
                      },
                      on: {
                          click: () => {
                              //this.show(params.index)
                          }
                      }
                  }, this.$t('common.Modify')),
                  h('Button', {
                      props: {
                          type: 'text',
                          size: 'small'
                      },
                      style: {
                          color:'#ed3f14'
                      },
                      on: {
                          click: () => {
                              //this.show(params.index)
                          }
                      }
                  }, this.$t('common.Delete'))
              ]);
          }
        }
      ],
      data1 :[
        {
          SID:'0001',
          user:'qweqwe',
          category:'管理角色',
          power:'高级权限',
          date:'2018-02-03 10:30:20',
          belong:'客服',
          operation:''
        },
        {
          SID:'0002',
          user:'sdfsdf',
          category:'普通角色',
          power:'普通权限',
          date:'2018-02-03 10:30:20',
          belong:'评级分析师',
          operation:''
        },
        {
          SID:'0003',
          user:'qwwqf',
          category:'管理角色',
          power:'超级权限',
          date:'2018-02-03 10:30:20',
          belong:'网站管理员',
          operation:''
        },
        {
          SID:'0004',
          user:'xdsfsdz',
          category:'普通角色',
          power:'基本权限',
          date:'2018-02-03 10:30:20',
          belong:'个人客户',
          operation:''
        },
        {
          SID:'0005',
          user:'dfgdfgd',
          category:'普通角色',
          power:'基本权限',
          date:'2018-02-03 10:30:20',
          belong:'企业客户',
          operation:''
        },
      ]
    }
  },
  methods:{
    onedata(){
      //this.$router.push('/Add_article');
    },
    goto (index){
      this.$router.push('/'+index);
    }
  }
}
</script>

<style lang="css">
.System-role{
  
}
.System-role .search_criteria p{
  height: 30px;
}
.System-role .search_criteria{
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
</style>



